<template lang="html">
  <div>
    <h1>{{ title }}<small>{{time}}</small></h1>
    <div v-html="ArticleContent"></div>
  </div>
</template>
<script>
  import { mapGetters, mapState } from 'vuex'
  export default {
    name: 'blog-article',
    computed: {
      ...mapGetters(['ArticleContent']),
      ...mapState({
        title: state => state.article.curArticle.title,
        time: state => state.article.curArticle.time
      })
    },
    methods: {
      getArticle ({ id }) {
        this.$store.dispatch('getSingleArticleById', { id })
      }
    },
    destroyed () {
      this.$store.dispatch('clearArticle')
    },
    mounted () {
      this.getArticle({ id: this.$route.params.id })
    }
  }
</script>

<style lang="less" scoped>
</style>




