<template>
  <ul class="list-unstyled">
    <li class="mb-2" v-for="item in articleList" v-bind:key="item.id" v-on:click="goToArticle(item.id)">
      <h3 class="title">
        {{item.title}}
        <i class="iconfont icon-calendar time">{{item.time}}</i>
      </h3>
      <div class="desc mb-1">
        <img class="mr-2" v-bind:src="item.descImg" v-if="item.descImg" >
        <span>{{item.desc}}</span>
      </div>
      <div class="tags">
        <span class="label label-primary" v-for="(tag, index) in item.tags" v-bind:key="index" v-on:click.stop="goToTags(tag)">{{tag}}</span>
      </div>
    </li>
  </ul>
</template>

<script>
  import { mapState } from 'vuex'
  export default {
    name: 'blog-articleList',
    computed: {
      ...mapState({
        articleList: state => state.article.articleList
      })
    },
    methods: {
      getArticleList () {
        this.$store.dispatch('getArticleList', { pageIndex: 1 })
      },
      goToArticle (id) {
        this.$router.push({ name: 'Article', params: { id } })
      },
      goToTags (tag) {
        this.$router.push({ name: 'Tags', params: { tag } })
      }
    },
    mounted () {
      if (this.articleList.length === 0) {
        this.getArticleList()
      }
    }
  }
</script>

<style lang="less" scoped>
  li {
    background-color: #fff;
    padding: 20px 30px;
    box-shadow: 3px 2px 20px rgba(0,0,0,0.07);

    &:hover {
      box-shadow: 10px 8px 50px rgba(0, 0, 0, 0.07);
    }

    .title {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .time {
        display: block;
        font-size: 16px;
        color: #aaa;
        &:before {
          margin-right: 5px;
        }
      }
    }

    .desc {
      display: flex;
      align-items: flex-start;
      img {
        width: 100px;
      }
    }

    .tags {
      border-top: 1px solid #ddd;
    }

    .badge {
      margin-right: 5px;
      &:last-child {
        margin-right: 0;
      }
    }
  }

  .mb-2 {
    margin-bottom: 20px;
  }

  .mb-1 {
    margin-bottom: 10px;
  }
</style>



