<template lang="html">
  <div>
    <div v-for="item in artsTag">
      <h3>{{ item.year }}</h3>
      <ul>
        <li v-for="info in item.data">
          <div>{{info.title}}</div>
          <div>{{info.time}}</div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
  import { mapState } from 'vuex'
  export default {
    computed: {
      ...mapState(['category']),
      ...mapState({
        artsTag: state => state.article.artsTag
      })
    },
    methods: {
      getArticles (tag) {
        this.$store.dispatch('getArticlesByTags', { tag })
      }
    },
    mounted () {
      this.getArticles(this.$route.params.tag)
    }
  }
</script>
<style lang="less" scoped>
</style>



