import axios from 'axios'

export default {
  getArticleList ({ pageIndex }) {
    return axios.get(`/doc/work/p-${pageIndex}.json`)
  },
  getSingle ({ title }) {
    return axios({
      method: 'get',
      url: `/doc/work/${title}/index.md`
    })
  },
  getTagsIndex () {
    return axios({
      methods: 'get',
      url: `doc/work/tagsIndex.json`
    })
  }
}



