import Vue from 'vue'
// import Router from 'vueRouter'
import Router from 'vue-router'

import ArticleList from '@/views/ArticleList'
import Article from '@/views/Article'
import Life from '@/views/Life'
import Tags from '@/views/Tags'
import Mobile from '@/views/Mobile'

Vue.use(Router)

let router = new Router({
  routes: [
    { path: '/', redirect: '/work' },
    { path: '/work', name: 'Work', component: ArticleList },
    { path: '/work/article/:id', name: 'Article', component: Article },
    { path: '/work/tags/:tag', name: 'Tags', component: Tags },
    { path: '/life', name: 'Life', component: Life },
    { path: '/mobile', name: 'Mobile', component: Mobile },
    { path: '*', redirect: '/work' }
  ]
})

export default router



